.inProgress {
    border-bottom: 2px solid #1c87c3 !important;
  }
  .container {
   
    display: flex !important;
    .barrier {
      border-left: 3px solid #f8f8f8;
      margin: 17px 0;
    }
    .button {
      padding: 10px;
      width: -moz-fit-content;
      width: fit-content;
      min-width: 170px;
      .completedBox {
        background: #d9f5e4 !important;
        svg > g > path,
        svg > path {
          stroke: #1f9a50 !important;
        }
      }
      .inProgressBox {
        background: #cdecfe !important;
        svg > g > path,
        svg > path {
          stroke: #1c87c3 !important;
        }
      }
      .logoBox {
        padding: 7px 9px;
        background: #f6f6f6;
        border-radius: 10px;
        margin-right: 9px;
        width: 40px;
        height: 40px;
  
        svg > g > path,
        svg > path {
          stroke: #d2d3d4;
        }
      }
      .content {
        line-height: 12px;
        display: flex !important;
        justify-content: space-between;
        flex-direction: column;
        height: 35px;
        .completedStatus {
          color: #1f9a50 !important;
        }
        .inProgressStatus {
          color: #1c87c3 !important;
        }
        .status {
          font-size: 10px;
          font-weight: 600;
          color: #d2d3d4;
        }
  
        .name {
          font-size: 13px;
          font-weight: 600;
          color: #090909;
          line-height: normal;
        }
      }
    }
  }
  
  .addUnitTab {
display: flex;
justify-content: start;
align-items: center;
border-bottom: 3px solid #f8f8f8;
  }